<template>
    <div id="App" :class="{Closed: !openned}">
        <div class="RankingBox">
            <ul class="RankingList">
                <li class="RankingItemBox">
                    <p class="RankingPosition">#</p>
                    <p class="RankingName">{{translate.TR_CREWNAME}}</p>
                    <p class="RankingName">{{translate.TR_CREATEDAT}}</p>
                    <p class="RankingName">{{translate.TR_RANKMEMBERS}}</p>
                    <p class="RankingKills">{{translate.TR_KILLS}}</p>
                </li>
                <li class="RankingItemBox" 
                v-for="(item, index) in crews"
                :key="index">
                    <p class="RankingPosition">{{index+1}}</p>
                    <p class="RankingName">{{item.name.length > 32 ? item.name.substr(0, 32) : item.name}}</p>
                    <p class="RankingName">{{item.createdat}}</p>
                    <p class="RankingName">{{item.members}}</p>
                    <p class="RankingKills">{{item.kills}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data: function(){
        return({
            crews : [
               
            ],

            NameResource: "CrewSystem",
            translate:{
                TR_CREWNAME : "Crew Name",
                TR_CREATEDAT: "Created At",
                TR_RANKMEMBERS: "Num Members",
                TR_KILLS : "Kills"
            },
            openned : false,
        })
    },
    methods:{
        close: function(){
            fetch(`https://${this.NameResource}/close`, {
                method: 'POST',
                body: JSON.stringify({})
            }); 
            this.openned = false;
        },
        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.configs){
                this.NameResource = lua.NameResource;
                this.translate = lua.translate;
            }
            else if(lua.openRank){
                this.crews = lua.crews;
                this.openned = true;
            }
        },
        keyPress: function(event){
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        }
    },
    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    }
}
</script>

<style>
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        list-style: none;

        color: #617185;
        font-family: 'Roboto', sans-serif;
    }
    .Closed{
        display: none !important;
    }
    .RankingBox{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        width: 80vw;
        height: 80vh;
        overflow-y: auto;
        overflow-x: hidden;

        background-color: rgb(248, 248, 248);

        padding-right: 1vh;
    }

    .RankingBox::-webkit-scrollbar-track {
        background-color: #ffffff;
    }

    .RankingBox::-webkit-scrollbar {
        width: 0.5vh;
        background: rgba(156, 54, 54, 0.8);
    }
    .RankingBox::-webkit-scrollbar-thumb {
        background: #617185b2;
        border-radius: 20px;
    }

    .RankingItemBox{
        display: flex;
        flex-direction: row;

        padding: 2vh;

        border-bottom: 1px solid #617185b2;
    }

    .RankingItemBox:hover{
        background-color: rgb(39, 39, 39);
    }
    .RankingItemBox:hover p{
        color: white;
    }

    .RankingItemBox:first-child:hover{
        background-color: #617185;
    }

    .RankingItemBox:first-child{
        background-color: #617185;
    }

    .RankingItemBox:first-child p{
        color: white;
    }

    .RankingItemBox p {
        font-size: 3vh;
    }

    .RankingPosition, .RankingKills{
        width: 10%;
    }

    .RankingName{
        /* flex: 1; */
        width: 25%;
    }
</style>